import React, {ReactNode, useEffect, useState} from "react";
import {
  HashRouter as Router,
  Switch,
  Route,
  Link, Redirect,
} from "react-router-dom";
import {Layout, Menu} from 'antd';
import styles from './App.module.scss'
import HeaderInfo from "./pages/HeaderInfo";
import {IRouter, routesConfig} from "./common/routes-config";
import LoginCom from "./pages/Login";
import {isLogin} from "./common/utils";

const { SubMenu } = Menu;
const { Sider } = Layout;

const App = () => {
  const [subMenu, setSubMenu] = useState<string[]>([]);
  const [selectedMenu, setSelectedMenu] = useState<string[]>(['dataMonitor']);
  const getMenu = (routesConfig: IRouter[]) => {
      return routesConfig.map((route: IRouter) => {
        if(route?.subRoutes?.length) {
            return <SubMenu key={route.key} icon={route.icon} title={route.name}>
                {getMenu(route.subRoutes)}
            </SubMenu>
        }
        return <Menu.Item key={route.key} icon={route.icon}>
            <Link to={`${route.link}` || '/'}>{route.name}</Link>
        </Menu.Item>
      })
  };

  const getRouter = (routesConfig: any) => {
      return routesConfig.map((item: IRouter) => {
        if (item?.subRoutes?.length) {
          return getRouter(item.subRoutes)
        }
        return ({link: item.link, component: item.component})
      })
  };

  const getSubMenu = (routesConfig: IRouter[]) => {
      return routesConfig.map((item: IRouter) => {
        if (item?.subRoutes?.length) {
            setSubMenu(prev => [...prev, item.key]);
            getSubMenu(item.subRoutes);
        }
      })
  };

  useEffect(() => {
      getSubMenu(routesConfig);
  }, []);

  useEffect(() => {
    const { hash } = window.location;
    setSelectedMenu([hash.split('/')[1] || 'dataMonitor'])
  }, [window.location.hash]);

  return (
      <div className={styles.app}>
        <Router>
          <Switch>
            <Route path="/login">
              <LoginCom />
              {!isLogin() && <Redirect to="/login" />}
            </Route>
          </Switch>
          {isLogin() ? <>
            <HeaderInfo />
            <Layout style={{ height: '100%' }}>
              <Sider
                collapsible={false}
                width={300}
                style={{height: 'calc(100vh - 60px)'}}
              >
                <Menu
                  selectedKeys={selectedMenu}
                  openKeys={[...new Set(subMenu)]}
                  onClick={({key}: {key: any}) => setSelectedMenu([key])}
                  mode="inline"
                  theme="dark"
                  style={{height: '100%', overflow: 'auto'}}
                  onOpenChange={(openKeys: any[]) => setSubMenu(openKeys)}
                >
                  {getMenu(routesConfig)}
                </Menu>
              </Sider>
              <div className={styles.content}>
                <Switch>
                  {getRouter(routesConfig).flat(5).map(
                    (item: {link: string, component: ReactNode}) =>
                      <Route path={item.link} exact>
                        {item.component}
                      </Route>)
                  }
                </Switch>
              </div>
            </Layout>
          </> : <LoginCom />}
        </Router>
      </div>
  );
};

export default App;
