import React, { useState } from "react";
import { Tabs } from 'antd';
import './index.scss';

const { TabPane } = Tabs;

const Equipment = () => {
    return <div className="equipment-container ceiling">
        <Tabs defaultActiveKey="1">
            <TabPane tab="设备功能" key="1">
                <ul>
                    <li>负压换气机 - 内置紫外线消杀模块</li>
                    <li>人体接近感知开机</li>
                    <li>门磁触发开机</li>
                    <li>远程APP开机</li>
                    <li>定时开机</li>
                </ul>
            </TabPane>
            <TabPane tab="温湿度检测" key="2">
                <ul>
                    <li>温湿度检测</li>
                </ul>
            </TabPane>
            <TabPane tab="运行状态检测" key="3">
                <ul>
                    <li>运行状态检测数据</li>
                </ul>
            </TabPane>
        </Tabs>
    </div>;
}

export default Equipment;
