import React, { useState } from "react";
import { Tabs } from 'antd';
import './index.scss';

const { TabPane } = Tabs;

const EquipmentCeiling = () => {
    return <div className="equipment-container window">
        <Tabs defaultActiveKey="1">
            <TabPane tab="负离子发生器" key="1">
                <ul>
                    <li>负离子发生器状态</li>
                    <li>负离子发生器数据</li>
                </ul>
            </TabPane>
            <TabPane tab="紫外线灯管" key="2">
                <ul>
                    <li>紫外线灯管</li>
                    <li>使用时长</li>
                    <li>消杀状态</li>
                </ul>
            </TabPane>
            <TabPane tab="光触媒模块" key="3">
                <ul>
                    <li>光触媒模块数据</li>
                </ul>
            </TabPane>
        </Tabs>
    </div>;
}

export default EquipmentCeiling;
